<template>
    <div class= "login">
        <div class = "max-width">
            <div class="logo">
                <img class="img-logo" src="https://user-images.githubusercontent.com/81811743/113415464-d8490e80-93f1-11eb-8131-05fbfe03cea4.jpg">
            </div>
            <div class="login-header">
                Login to your account
            </div>

            <form class="login-form" id="login-form"  action="/action_page.php">
                <div class="field-username">
                    <input class="field-name" v-model="emailString" type="text" placeholder="Name" required>
                </div>

                <div class="field-password">                 
                <input class="field-password" v-model="passwordString" type="password" placeholder="Password" required>
                <!-- <a class="eye" v-on:click="toggle"> -->
                    <i class="fa fa-eye" id="eye"></i>
                <!-- </a> -->
                </div>
                
                <div class="button">
                    <button class="btn" type="submit" @click="handleSubmit(emailString, passwordString)">Login</button>
                </div>
            </form>

            <div class="footer-header">
                ---------------------------------------------------
            </div>

            <div class="footer-content">
                Private policy | Term of use
            </div>

        </div>
    </div>
</template>


<script>
import axios from 'axios'
export default {
    name : 'Login',
    data() {
        return {
            user : []
        }
    },
    methods:{
        handleSubmit(emailString, passwordString) {
            const loginUser = {
                //id : this.id, 
                email: emailString,
            }

            const {email} = loginUser;

            
          axios.get(`http://localhost:5000/api/users/${email}`,  {mode:'cors'},{
                email
            })

            .then( res => this.user = res.data)
      
            .catch(error =>{
                return Promise.reject(error);
            })

            console.log(this.user);

            if(this.user.password == passwordString){
                if (this.user.role == "Admin") {
                    this.$router.push('/admin');
                }
                else{
                    this.$router.push('/email')
                }
            }
            else {
                alert('Email or Password Did not match');
            }
           
            //localStorage.setItem('token', response.data.token);
        }
        
    }
}
</script>

